.experience-card {
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    padding: 1.5rem;
    margin: 0 1rem;
    text-align: center;
    background: rgba(22, 17, 47, 0.398);
    backdrop-filter: blur(1rem);
}

.experience-card h6 {
    font-size: 1.05rem;
    font-weight: 500;
    margin-bottom: 0.7rem;
}

.duration-job-container {
    display: flex;
}

.duration {
    display: inline-block;
    font-size: 0.7rem;
    font-weight: 400;
    background: rgba(103, 81, 185, 0.5);
    border-radius: 0.3rem;
    padding: 0.4rem 0.6rem;
    margin-bottom: 1.3rem;
    white-space: nowrap;
}

.job {
    font-size: 0.9rem;
    font-weight: 400;
    margin-left: 9rem;
    text-decoration: none;
    color: #fff;
    white-space: nowrap;
}

.experience-card ul li {
    list-style: none;
    font-size: 0.8rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
    position: relative;
}

.experience-card ul li::after {
    content: " ";
    width: 0.5rem;
    height: 0.5rem;
    background-color: #6751b9;
    border-radius: 0.5rem;
    position: absolute;
    left: -1.3rem;
    top: 0.35rem;
}

@media (max-width: 768px) {
    .experience-card {
        margin: 0;
    }

    .experience-card h6 {
        font-size: 0.95rem;
    }

    .experience-card ul li {
        font-size: 0.75rem;
    }
} 